.burgerContent{
    width: 100%;
    min-height: 100vh;
    max-height: auto;
    background: linear-gradient(#9198e5, #e66465);
}
.parent{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.total{
    width: 100%;
    height: 40px;
    margin: auto;
    text-align: center;
    font-size: 24px;
}
.crimson{
    color: crimson;
}
.burgerIngredients{
   height: auto;
}
.burgerIngredients p{
    margin: 14px auto;
    text-align: center;
}
.topSide{
    background-color: #e07a04f1;
    height: 80px;
    width: 300px;
    text-align: center;
    border-top-right-radius: 70px;
    border-top-left-radius: 70px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 5px;
    display: block;
    position: relative;
    z-index: 1;
}

.parentSedana{
    position: absolute;
    right: 10px;
    left: 10px;
    top: 10px;
    bottom: 10px;
    width: 80%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    text-align: center;
    padding: 20px;
}

.sedana{
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-bottom:8px solid #333;
    border-radius: 40px;
    transform: rotate(90deg);
}

.sedana2{
    margin-top: 8px;
}
.sedana3{
    margin-left:12px;
    margin-top:10px;
}

.bottomSide{
    background-color: #e07a04f1;
    background-size: 300px;
    height: 75px;
    width: 300px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 70px;
    border-bottom-right-radius: 70px;
    margin: auto;
    margin-bottom: 10px;
    display: block;
    
}
.lettuseSide{
    height: 20px;
    width: 310px;
    margin: auto;
    display: block;
    background-color: rgba(30, 233, 40, 0.76);
    border-radius: 20px;
}
.tomatoSide{
    height: 15px;
    width: 320px;
    margin: auto;
    border-radius: 20px;
    background-color: rgba(247, 27, 27, 0.918);
    display: block;
}
.cheeseSide{
    background-color: rgba(231, 202, 38, 0.829);
    height: 10px;
    width: 306px;
    border-radius: 10px;
    margin: auto;
    display: block;
}
.meatSide{
   background-color: rgb(97, 41, 2);
    height: 36px;
    width: 316px;
    border-radius: 20px;
    margin: auto;
    display: block;
}

.ingredientsBlock{
    /* background-color: #cad1d3f1; */
    margin: 30px auto 0;
    display: table;
    max-width: 200px;
    width: auto;
    height: 300px;
    border-radius: 3px;
    padding: 20px;
    text-align: center;
    box-shadow: 0px 0px 30px 20px rgba(238, 234, 234, 0.774);
}
.ingredientsBlock p{
    padding: 0;
    margin: 0;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
}
.ingrBtns{
    margin: 0 0 20px;
    display: flex;
    justify-content: space-between;
}
.ingrBtn{
    margin: 5px;
    border: 0;
    background-color: #c9c6c6;
    color: #fff;
    padding: 5px;
    cursor: pointer;
    display: block;
    width: 95px;
}